body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.breed-select {
  width: 260px;
  border: 2px solid gray;
  border-radius: 5px;
  margin-top: 20px;
  margin-left: 30px;
}

.loader {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader-text {
  text-align: center;
  margin-top: 20px;
  font-weight: bold;
}

.error {
  text-align: center;
  color: red;
  margin-top: 20px;
  font-weight: bold;
}

.cat-info {
  margin-top: 40px;
}

.cat-info .cat-description {
  flex-flow: wrap;
  align-items: center;
  gap: 25px;
  margin-bottom: 20px;
  display: flex;
}

.cat-info .cat-description img {
  width: 280px;
  height: auto;
  border: 2px solid #ccc;
  border-radius: 10px;
  flex-shrink: 0;
  justify-content: center;
}

.cat-info .cat-description .cat-text {
  border: 2px solid #ccc;
  border-radius: 10px;
  flex-grow: 1;
  padding: 10px;
}

.is-hidden {
  display: none;
}

.ss-main {
  width: 65%;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 800px) {
  .cat-info .cat-description {
    flex-flow: unset;
  }
}

